<!--政策-->
<template>
  <div class="notice-form">
   <!-- 锚点按钮 -->
    <el-card class="top-margin-10 classBut" :class="{fixed:fixed}" >
    <div  class="step" >
        <el-button :class="classChoose==5?'class-Choose':''"   @click="jump('n-info')"   >基本信息</el-button> 
        <el-button :class="classChoose==6?'class-Choose':''"   @click="jump('n-subsidies')" >补贴项目信息</el-button>  
        <el-button :class="classChoose==7?'class-Choose':''"   @click="jump('n-interpretation')"  >政策解读</el-button>  
        <el-button :class="classChoose==8?'class-Choose':''"   @click="jump('n-original')"  >政策原文</el-button> 
    </div> 
    </el-card>

    <!--基本信息-->
    <el-form ref="form"  id="n-info"  :rules="rules" :model="form" label-width="auto" label-position="left" @submit.native.prevent>
      <el-card class="top-margin-10">
        <div slot="header" class="clearfix">
          <span style="color: #6DDD81; font-weight: bold;width: 3px;">|</span>
          <span class="index-content-title">&nbsp;基本信息</span>
        </div>
        <div>
          <el-row type="flex" :gutter="10">
            <el-col :span="20" :offset="2">
              <div class="address-bar">
                <label class="address-title">
                  <span style="color: #F56C6C;">*</span> 地区：
                </label>
                <div class="address-group" v-if="!isCheck">
                  <el-select @change='changeProvince' class="address-input" clearable v-model="form.provinceId"
                             placeholder="省份">
                    <el-option v-for="item in province_list" :key="item.dictId" :label="item.dictName"
                               :value="item.dictId"></el-option>
                  </el-select>
                  <el-select @change='changeCity' class="address-input" clearable v-model="form.cityId"
                             placeholder="城市">
                    <el-option v-for="item in city_list" :key="item.dictId" :label="item.dictName"
                               :value="item.dictId"></el-option>
                  </el-select>
                  <el-select @change='changeDistrict' class="address-input" clearable v-model="form.districtId"
                             placeholder="县区">
                    <el-option v-for="item in district_list" :key="item.dictId" :label="item.dictName"
                               :value="item.dictId"></el-option>
                  </el-select>
                  <el-select class="address-input" clearable v-model="form.townId" placeholder="镇街">
                    <el-option v-for="item in town_list" :key="item.dictId" :label="item.dictName"
                               :value="item.dictId"></el-option>
                  </el-select>
                </div>
                <div v-else>{{form.provinceName}}{{form.cityName}}{{form.districtName}}{{form.townName}}</div>
              </div>
            </el-col>
          </el-row>
          <el-row type="flex" :gutter="10">
            <el-col :span="10" :offset="2">

              <div class="address-bar" style="padding-left: 0">
                <label class="address-title">
                  <span style="color: #F56C6C;">*</span> 发布单位：
                </label>
                <el-input v-if="!isCheck" v-model.trim="form.publishOrgan" placeholder="请输入发布单位"
                          maxlength="50"></el-input>
                <div v-else>{{form.publishOrgan}}</div>
              </div>

            </el-col>
            <el-col :span="10">
              <div class="address-bar" style="padding-left: 0">
                <label class="address-title" style="padding-left: 20px;">
                  <span style="color: #F56C6C;">*</span> 发布日期：
                </label>
                <el-date-picker v-if="!isCheck" type="date" v-model="form.publishDate" placeholder="选择日期"
                                value-format="yyyy-MM-dd"></el-date-picker>
                <div v-else>{{form.publishDate}}</div>
              </div>
            </el-col>
          </el-row>
          <el-row type="flex" :gutter="10">
            <el-col :span="10" :offset="2">
              <div class="address-bar" style="padding-left: 9px;">
                <label class="address-title">
                  扶持方式：
                </label>
                <el-select v-if="!isCheck" style="width: 100%;" v-model="form.subsidyWay" clearable
                           placeholder="选择扶持方式">
                  <el-option v-for="item in supply_way_list" :key="item.dictId" :value="item.dictId"
                             :label="item.dictName"></el-option>
                </el-select>
                <div v-else>{{form.subsidyWay==1?'事后补偿':form.subsidyWay==2?'无偿补偿':form.subsidyWay==3?'其他':'-'}}</div>
              </div>
            </el-col>
            <el-col :span="10">
              <div class="address-bar" style="padding-left: 0">
                <label class="address-title" style="padding-left: 29px;">
                  补助行业：
                </label>
                <el-select v-if="!isCheck" @focus="getCommonData" filterable clearable v-model="form.subsidyIndustry"
                           placeholder="选择补助行业">
                  <el-option v-for="item in supply_industry_list"
                             :key="item.dictId"
                             :value="item.dictId"
                             :label="item.dictName"></el-option>
                </el-select>
                <div v-else>{{form.subsidyIndustryName}}</div>
              </div>
            </el-col>
          </el-row>
          <el-row type="flex" :gutter="10">
            <el-col :span="10" :offset="2">
              <div class="address-bar" style="padding-left: 0">
                <label class="address-title">
                  <span style="color: #F56C6C;">*</span> 难易程度：
                </label>
                <el-select v-if="!isCheck" style="width: 100%;" clearable v-model="form.applyDifficulty"
                           placeholder="选择难易程度">
                  <el-option v-for="item in difficulty_list"
                             :key="item.dictId"
                             :value="item.dictId"
                             :label="item.dictName">
                  </el-option>
                </el-select>
                <div v-else>{{form.applyDifficulty == 1?'普惠性':form.applyDifficulty ==2?'竞争性':'-'}}</div>
              </div>
            </el-col>
            <el-col :span="10">
              <div class="address-bar" style="padding-left: 33px;">
                <label class="address-title">
                  <span style="color: #F56C6C;">*</span> 有效期：
                </label>
                <el-date-picker
                  type="daterange"
                  unlink-panels
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  value-format="yyyy-MM-dd"
                  v-model="termDate"
                  v-if="!isCheck"
                />
                <div v-else>
                  {{form.validDateBegin&& form.validDateEnd?termDateName:'-'}}
                </div>
              </div>
            </el-col>
          </el-row>
          <el-row type="flex" :gutter="10">
            <el-col :span="20" :offset="2">
              <el-form-item label="备注：">
                <el-input maxlength="300" v-if="!isCheck" v-model="form.noticeRemark" type="textarea" :rows="3"
                          placeholder="请输入备注"></el-input>
                <span v-else>{{form.noticeRemark && form.noticeRemark != ''? form.noticeRemark : '-'}}</span>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </el-card>
    </el-form>

    <!--补贴项目信息-->
    <el-card  id="n-subsidies"   class="top-margin-10 project-massage">
      <div slot="header" class="clearfix">
        <div>
          <span style="color: #05AAFF; font-weight: bold;width: 3px;">|</span>
          <span>&nbsp;补贴项目信息</span>
          <span style="margin-left: 10px;">
            <el-checkbox v-model="isShowTerm">
              显示申报条件
            </el-checkbox>
          </span>
        </div>

        <button v-if="!isCheck" class="select_project_bt select-project float-right" @click="goSelectProject">
          选择项目
        </button>
      </div>
      <div class="list-box">
        <div class="item" v-for="(item,index) in select_projects">
          <div class="main-project">
            <div class="left">
              <div class="index">{{index+1}}</div>
              <div class="form-box">
                <div class="project-form">

                  <!--项目名称-->
                  <div class="project-name">
                    项目名称：{{item.projectName}}
                    <div class="right" v-if="!isCheck">
                      <button class="del-project-btn add-project-btn" @click="addSubsidyHandle(item,index)">新增补助
                      </button>
                      <button class="del-project-btn" @click="removeProjectItem(item,index)">移除</button>
                    </div>
                  </div>


                  <!--项目级别-->
                  <div class="project-sub-list" v-for="(data,index) in item.policyNoticeProjectRelList">
                    <div class="left-sub">
                      <div class="three-input">
                        <div class="project-level">
                          <span class="xin">*</span>项目级别：
                          <el-select v-if="!isCheck" v-model="data.projectLevel">
                            <el-option label="国家级" :value="1"></el-option>
                            <el-option label="省级" :value="2"></el-option>
                            <el-option label="市级" :value="3"></el-option>
                            <el-option label="区县级" :value="4"></el-option>
                            <el-option label="镇街级" :value="5"></el-option>
                          </el-select>

                          <div v-else>
                            {{data.projectLevel==1?'国家级':data.projectLevel==2?'省级':data.projectLevel==3?'市级':data.projectLevel==4?'区县级':data.projectLevel==5?'镇街级':'-'}}
                          </div>
                        </div>
                        <div class="project-min-subsidy">
<!--                          <span class="xin">*</span>-->
                          最低补助：
                          <el-input v-if="!isCheck" v-model="data.subsidyPriceMin" class="money-input"
                                    placeholder="最低补助" onkeyup="value=value.match(/^\d*(\.?\d{0,2})/g)[0] || null" maxlength="4">
                            <template slot="append">万元</template>
                          </el-input>
                          <div v-else>{{data.subsidyPriceMin&&data.subsidyPriceMin!==''?data.subsidyPriceMin + '万元':0 +
                            '万元'}}
                          </div>
                        </div>
                        <div class="project-max-subsidy">
<!--                          <span class="xin">*</span>-->
                          最高补助：
                          <el-input v-if="!isCheck" onkeyup="value=value.match(/^\d*(\.?\d{0,2})/g)[0] || null" maxlength="4"
                                    v-model="data.subsidyPriceMax" class="money-input"
                                    placeholder="最高补助">
                            <template slot="append">万元</template>
                          </el-input>

                          <div v-else>{{data.subsidyPriceMax&&data.subsidyPriceMax!==''?data.subsidyPriceMax+ '万元':0+'万元'}}
                          </div>
                        </div>
                      </div>
                      <div class="remarks-form">
                        补助详情：
                        <el-input maxlength="4000" v-if="!isCheck" v-model="data.subsidyDesc" resize="none"
                                  type="textarea"/>
                        <div style="white-space: normal;" v-else>{{data.subsidyDesc}}</div>
                      </div>
                    </div>
                    <i v-if="item.isDel && !isCheck" class="del-sub-list el-icon-delete"
                       @click="delProjectHandle(item,index)"></i>
                  </div>


                  <!--申报条件-->
                  <div class="main-declare" v-if="isShowTerm">
                    <div class="title">
                      <div class="title-name">申报条件</div>
                      <button class="add-term-btn" @click="addTermHandle(item)" v-if="!isCheck">新增条件</button>
                    </div>

                    <div class="content">
                      <div class="list" v-for="(list,index) in item.policyApplyConditionVoList">
                        <div class="list-num">{{index+1}}</div>
                        <div class="list-body">
                          <div class="term-title">
                            <div>政策条件描述：{{list.conditionDesc}}</div>
                            <div class="title-btn-group">
                              <button class="btn" @click="addSentenceHandle(list,index)"
                                      v-if="!isCheck && list.autoMatch != 0">新增语句
                              </button>
                              <button class="btn" @click="editTermHandle(list,index)" v-if="!isCheck">编辑</button>
                              <button class="btn" @click="delTermHandle(list,index)" v-if="!isCheck">删除</button>
                            </div>
                          </div>
                          <div class="sub-title">
                            <div>权重：{{list.weightScore}}分</div>
                            <div>匹配类型：{{list.autoMatch ==0?'人工匹配':'自动匹配'}}</div>
                            <div v-if="list.autoMatch ==1">子表达式：{{list.conditionExpression}}</div>
                          </div>
                          <div class="sub-list">
                            <template
                              v-if="list.policyApplyConditionSqlVoList && list.policyApplyConditionSqlVoList.length !==0">
                              <div class="item" v-for="(childList,num) in list.policyApplyConditionSqlVoList">
                                <div v-if="childList.customSql == ''" class="left">
                                  <div>#{{num+1}}.分类：{{childList.tabelType}}</div>
                                  <div>数据表：{{childList.zhTableName}}</div>
                                  <div>select字段：{{childList.zhSelectFieldName}}</div>
                                  <div>where字段：{{childList.zhWhereFieldName}}</div>
                                  <div>对比符号：{{childList.compareMark}}</div>
                                  <div>对比值：{{childList.compareValue}}</div>
                                  <div v-if="childList.selectFieldName == '[count]'">统计：是</div>
                                </div>
                                <div v-else class="left">
                                  #{{num+1}}.自定义SQL(支持系统变量)：{{childList.customSql}}
                                </div>
                                <div class="right" v-if="!isCheck">
                                  <button class="btn" @click="editMentHandle(list,index,childList,num)">编辑</button>
                                  <button class="btn" @click="delMentHandle(list,index,childList,num)">删除</button>
                                </div>
                              </div>
                            </template>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>


                  <!--材料清单-->
                  <div class="main-declare" style="margin-top: 30px;">
                    <div class="title">
                      <div class="title-name">材料清单</div>
                      <button class="add-term-btn" @click.prevent="addMaterialBtnHandle(index)" v-if="!isCheck">新增材料
                      </button>
                    </div>

                    <div class="content">
                      <div class="list" v-for="(material,nums) in item.policyApplyMaterialLists">
                        <div class="list-num" style="padding-top: 0;">{{nums+1}}.</div>
                        <div class="list-body">
                          <div class="term-title" style="align-items: normal;">

                            <div class="file-type-bar">
                              <div style="width: 33%">附件分类：{{material.materialTypeName}}</div>
                              <div style="width: 33%;padding-right: 20px;">要求：{{material.materialDesc}}</div>
                              <div style="width: 33%" class="file-model">
                                <div style="white-space: nowrap;">模板文件：</div>
                                <ul class="files-list">
                                  <li v-for="(childMat,childNum) in material.templateFileIds">
                                    {{childNum+1}}.{{childMat.fileName}}<i
                                    @click="deleteTemplateFile(index,nums,childNum)"
                                    v-if="!isCheck"
                                    class="icon-del-file el-icon-delete"></i>
                                  </li>
                                </ul>
                              </div>
                            </div>

                            <div class="title-btn-group">
                              <button class="btn" @click="deleteDetailed(index,nums)" v-if="!isCheck">移除</button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </el-card>

    <!--政策解读-->
    <el-card  id="n-interpretation"   class="top-margin-10">
      <div slot="header" class="clearfix">
        <span style="color: #FFA96A; font-weight: bold;width: 3px;">|</span>
        <span class="index-content-title">&nbsp;政策解读</span>
      </div>
      <div>
        <el-row :gutter="12">
          <el-col :span="20" :offset="2">
            <Tinymce v-if="!isCheck" v-model="form.noticeSummary" :height=400 ref="editor"/>
            <p class="editorHtml" v-else v-html="form.noticeSummary">{{form.noticeSummary}}</p>
          </el-col>
        </el-row>
      </div>
    </el-card>

    <!--原文-->
    <el-card  id="n-original"   class="top-margin-10">
      <div slot="header" class="clearfix">
        <span style="color: #799CFF; font-weight: bold;width: 3px;">|</span>
        <span class="index-content-title">&nbsp;原文</span>
      </div>
      <div>
        <el-row :gutter="12">
          <el-col :span="20" :offset="2">
            <Tinymce v-if="!isCheck" v-model="form.noticeContent" :height=400 ref="editor2"/>
            <p class="editorHtml" v-else v-html="form.noticeContent">{{form.noticeContent}}</p>
          </el-col>
        </el-row>
        <el-row :gutter="12" class="top-margin-10">
          <el-col :span="20" :offset="2">
            <el-form-item label="原文链接：">
                                <span v-if="isCheck">
                                  <a :href="form.sourceUrl" target="_blank">{{form.sourceUrl}}</a>
                                </span>

              <input v-if="!isCheck" v-model="form.sourceUrl" type="text" placeholder="请输入原文链接"
                     class="input_text float-left width-800"/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="12">
          <el-col :span="20" :offset="2">
            <el-form-item label="政府文件补充：" prop="official_file">
              <el-upload style="display: inline-block;width:100%;"
                         ref="upload"
                         class="upload-box upload-box-width"
                         :http-request="handlePoliceUpload"
                         :file-list="fileList"
                         action=""
                         v-if="!isCheck"
                         :show-file-list="false">
                <el-button type="primary" size="small">点击上传</el-button>
              </el-upload>
              <div class="file-list" v-for="(item,index) in fileList">
                <div>{{item.fileName}}</div>
                <i v-if="!isCheck" class="icon-del-file el-icon-circle-close" @click="delFileHandle(index)"></i>
                <img class="down-files" v-else src="../../../static/down-img.png" @click="downFileHandle(item)" alt=""/>
              </div>
              <!--                            <p style="cursor:pointer;" v-for="(item,index) in fileList" v-else @click="downloadFile(item)">-->
              <!--                              {{ (index + 1 + '、') + item.name + '.' + item.fileType}}-->
              <!--                            </p>-->
            </el-form-item>
          </el-col>
        </el-row>
      </div>
    </el-card>

    <!--确认/取消-->
    <div class="center-items top-margin-30">
      <el-form-item>
        <el-button type="primary" v-if="isCreate" @click="noticeHandSubmit('form')" v-preventReClick>确认</el-button>
        <el-button type="primary" v-if="isEdit" v-else @click="noticeHandSubmit('form')" v-preventReClick>修改</el-button>
        <el-button @click="handlerCancel">取消</el-button>
      </el-form-item>
    </div>


    <!--选择项目--弹窗-->
    <el-dialog title="选择补贴项目" class="project-modal"
               :visible.sync="project_table_visible"
               :before-close="handleCloseProject"
               width="60%"
               destroy-on-close="true"
               v-dialogDrag
    >
      <!-- 搜索框 -->
      <div>
        <div class="projectChoose">
          <span>项目筛选：</span>
          <el-select size="mini"
                     clearable
                     multiple
                     class="inline-block right-margin-10"
                     placeholder="项目类型"
                     v-model="project_list_query.projectTypes"
                     @focus="getCommonData"
                     @change="typeChange"
          >
            <el-option v-for="item in type_list" :key="item.dictId" :value="item.dictId"
                       :label="item.dictName"></el-option>
          </el-select>
          <el-select remote
                     :remote-method="companyRemoteMethod"
                     v-loadMore="loadMore"
                     @change="projectChange"
                     :loading="loading" size="mini" clearable filterable
                     v-model="project_list_query.projectName" placeholder="项目"
                     class="inline-block right-margin-10"
          >
            <el-option v-for="item in projectDataList" :key="item" :value="item" :label="item"></el-option>
          </el-select>
          <!--          <el-select class="inline-block" multiple size="mini" clearable v-model="project_list_query.areaLevel"-->
          <!--                     placeholder="国家级别" @change="areaChange">-->
          <!--            <el-option label="国家级" :value="1"></el-option>-->
          <!--            <el-option label="省级" :value="2"></el-option>-->
          <!--            <el-option label="市级" :value="3"></el-option>-->
          <!--            <el-option label="区县级" :value="4"></el-option>-->
          <!--            <el-option label="镇街级" :value="5"></el-option>-->
          <!--          </el-select>-->
        </div>
        <!--        <div class="projectChoose">-->
        <!--          <span>地区：</span>-->
        <!--          <el-select @change='projectChangeProvince' clearable size="mini"-->
        <!--                     class="inline-block input-150 right-margin-10"-->
        <!--                     v-model="project_list_query.provinceId" placeholder="省份">-->
        <!--            <el-option v-for="item in province_list" :key="item.dictId" :label="item.dictName"-->
        <!--                       :value="item.dictId"></el-option>-->
        <!--          </el-select>-->
        <!--          <el-select @change='projectChangeCity' clearable size="mini" class="inline-block input-150 right-margin-10"-->
        <!--                     v-model="project_list_query.cityId" placeholder="城市">-->
        <!--            <el-option v-for="item in city_list" :key="item.dictId" :label="item.dictName"-->
        <!--                       :value="item.dictId"></el-option>-->
        <!--          </el-select>-->
        <!--          <el-select @change='projectChangeDistrict' clearable size="mini"-->
        <!--                     class="inline-block input-150 right-margin-10"-->
        <!--                     v-model="project_list_query.districtId" placeholder="县区">-->
        <!--            <el-option v-for="item in district_list" :key="item.dictId" :label="item.dictName"-->
        <!--                       :value="item.dictId"></el-option>-->
        <!--          </el-select>-->
        <!--          <el-select @change='projectChangeTownsId' clearable size="mini" class="inline-block input-150 right-margin-10"-->
        <!--                     v-model="project_list_query.townId" placeholder="镇街">-->
        <!--            <el-option v-for="item in town_list" :key="item.dictId" :label="item.dictName"-->
        <!--                       :value="item.dictId"></el-option>-->
        <!--          </el-select>-->
        <!--        </div>-->
      </div>
      <!--搜索到的总条数-->
      <div class="search-num">
        搜索到<span class="num">{{project_list_total}}</span>条记录
      </div>
      <!-- 表格 -->
      <el-table ref="dataTable" class="project-table"
                :data="project_list" border
                @select="onTableSelect"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="项目名称" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.projectName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="项目分类" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.projectTypeName}}</span>
          </template>
        </el-table-column>
        <el-table-column label="订单类型" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.orderType == 0 ? '项目型' : scope.row.orderType == 1 ? '知识产权型' : scope.row.orderType == 2 ? '财务型' : scope.row.orderType == 4 ? '项目审计型' : '-' }}</span>
          </template>
        </el-table-column>
      </el-table>

      <template v-if="project_list.length !== 0">
        <div class="get-more" v-if="project_list.length >= project_list_total">没有更多</div>
        <div class="get-more" v-else @click="getMoreProjectList"><span>加载更多</span></div>
      </template>

      <!-- 按钮 -->
      <span slot="footer" class="dialog-footer">
               <el-button class="sub-btn" @click="setProjectList">确定</el-button>
               <el-button @click="cloneProjectModal">取消</el-button>
            </span>
    </el-dialog>

    <!--新增条件--弹窗-->
    <el-dialog title="新增条件" class="editorUserModal" :visible.sync="addTermModal" width="600px"
               :before-close="callTermHandle" v-dialogDrag>
      <el-form :model="addTermParams" :rules="termParamsRules" ref="termParamsRules" label-width="88px">
        <el-form-item label="条件描述：" prop="conditionDesc" style="white-space: nowrap;">
          <el-input maxlength="500" show-word-limit v-model="addTermParams.conditionDesc" type="textarea" resize="none" :rows="3"
                    placeholder="请输入条件描述"></el-input>
        </el-form-item>
        <el-form-item class="modal-radio" label="匹配类型：">
          <el-radio-group v-model="addTermParams.autoMatch">
            <el-radio :label="0">人工匹配</el-radio>
            <el-radio :label="1">自动匹配</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="权重分数：" prop="weightScore">
          <el-input @input.native="inputXz($event,0)" oninput="value=value.replace(/[^0-9]/g,'')"
                    v-model="addTermParams.weightScore" placeholder="请输入权重分数"
                    maxlength="100"></el-input>
        </el-form-item>

        <el-form-item label="子表达式：" prop="conditionExpression" v-if="addTermParams.autoMatch == 1">
          <el-input v-model="addTermParams.conditionExpression" type="textarea" resize="none" :rows="3"
                    placeholder="请输入子表达式"></el-input>
        </el-form-item>

        <div class="system-btn">
          <el-button class="btn" @click="systemHandle">系统变量</el-button>
        </div>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button class="setBtn" @click="setAddTerm">确 定</el-button>
        <el-button class="cananl" @click="callTermHandle">取 消</el-button>
      </span>
    </el-dialog>

    <!--新增语句--弹窗-->
    <el-dialog title="新增语句" class="editorUserModal ment-modal" :visible.sync="addMentModal" width="600px"
               :before-close="callMentHandle" v-dialogDrag>
      <el-form ref="implementForm" label-width="88px">
        <el-form-item class="ment-text" label="条件描述：">
          <span>{{addSentenceParams.conditionDesc}}</span>
        </el-form-item>

        <el-form-item class="modal-radio" label="表分类：">
          <el-select v-model="addSentenceParams.tabelType" @change="tableTypeHandle" clearable>
            <el-option
              v-for="item in tableTypeData"
              :value="item.dictName"
              :label="item.dictName"
              :key="item.dictName"
            />
          </el-select>
        </el-form-item>
        <el-form-item class="modal-radio" label="form数据表：" v-if="addSentenceParams.tabelType !==''">
          <el-select v-model="addSentenceParams.tableName" @change="tableNameHandle" clearable>
            <el-option
              v-for="item in tableNameData"
              :value="item.tableName"
              :label="item.tableComment"
              :key="item.tableName"
            />
          </el-select>
        </el-form-item>

        <el-form-item label="select查询字段：" v-if="addSentenceParams.tableName !==''">
          <el-select v-model="addSentenceParams.selectFieldName" clearable>
            <el-option
              v-for="item in fileldNameData"
              :value="item.columnName"
              :label="item.columnComment"
              :key="item.columnName"
            />
          </el-select>
        </el-form-item>

        <el-form-item label="where筛选字段：" v-if="addSentenceParams.tableName !==''">
          <el-select v-model="addSentenceParams.whereFieldName" clearable>
            <el-option
              v-for="item in fileldNameData"
              :value="item.columnName"
              :label="item.columnComment"
              :key="item.columnName"
            />
          </el-select>
        </el-form-item>

        <el-form-item label="筛选符号：">
          <el-select v-model="addSentenceParams.compareMark" clearable>
            <el-option :value="'='">=</el-option>
            <el-option :value="'!='">!=</el-option>
            <el-option :value="'>'">></el-option>
            <el-option :value="'>='">>=</el-option>
            <el-option :value="'<'"><</el-option>
            <el-option :value="'<='"><=</el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="筛选值(支持系统变量)：">
          <el-input v-model="addSentenceParams.compareValue"/>
        </el-form-item>


        <el-form-item label="自定义SQL(支持系统变量)：">
          <el-input type="textarea" v-model="addSentenceParams.customSql" resize="none" :rows="3"
                    placeholder="请输入子表达式"></el-input>
        </el-form-item>

        <div class="system-btn">
          <button class="btn" @click.stop="systemHandle">系统变量</button>
        </div>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button class="setBtn" @click="setAddMent">确 定</el-button>
        <el-button class="cananl" @click="callMentHandle">取 消</el-button>
      </span>
    </el-dialog>

    <!--系统变量--弹窗-->
    <div class="system-model" v-show="systemModal" v-drag>
      <div class="modal-heards">
        系统变量
        <i class="icon-close el-icon-close" @click="systemModal = false"></i>
      </div>
      <div class="modal-content">
        <div class="sum-data">
          <el-input v-model="keyWord" placeholder="请输入关键字" style="width: 220px;"></el-input>
          <el-button @click="systemHandle">搜索</el-button>
        </div>
        <div class="sum-data">共<span class="num">{{systemVarDataTotal}}</span>条数据</div>
        <el-table :data="systemVarData" height="302" border>
          <el-table-column type="index" width="40"></el-table-column>
          <el-table-column prop="dictValue" label="变量名"></el-table-column>
          <el-table-column prop="dictName" label="中文名称"></el-table-column>
          <el-table-column prop="dictDesc" label="备注"></el-table-column>
        </el-table>
        <div class="sys-var-more">
          <div v-if="this.systemVarData.length<systemVarDataTotal" class="get-sys-more" @click="getMoreSysData">加载更多
          </div>
          <div style="padding-bottom: 10px;" v-else>没有更多了</div>
        </div>
      </div>
    </div>

    <!--新增材料--弹窗-->
    <el-dialog title="新增材料" class="editorUserModal ment-modal" :visible.sync="materialModal" width="600px" v-dialogDrag>
      <el-form :model="materialParams" :rules="materialRule" ref="materialForm" label-width="88px">
        <el-form-item style="white-space: nowrap;" class="modal-radio" label="附件分类：" prop="materialType">
          <el-select v-model="materialParams.materialType" clearable>
            <el-option
              v-for="item in annexTypeData"
              :value="item.dictId"
              :label="item.dictName"
              :key="item.dictId"
            />
          </el-select>
        </el-form-item>

        <el-form-item class="modal-radio" label="要求：" prop="materialDesc">
          <el-input maxlength="500" v-model="materialParams.materialDesc" type="textarea" resize="none" :rows="3"
                    placeholder="请输入要求"></el-input>
        </el-form-item>

        <el-form-item class="modal-radio" label="" style="margin-bottom: 0;">
          <div style="width: 88px;color: rgba(68, 68, 68, 0.75)"><span style="color:#F56C6C;margin-right: 4px;">　</span>模板：
          </div>
          <el-upload
            :http-request="handleTemplateUpload"
            class="image-uploader"
            :file-list="templateFileList"
            :show-file-list="false"
            action=""
          >
            <el-button class="set-file-btn">
              上传附件
            </el-button>
          </el-upload>
        </el-form-item>

        <!--文件列表-->
        <el-form-item class="modal-radio" label="　">
          <ul>
            <li v-for="(item,index) in templateFileList">
              {{item.fileName}} <i @click="delTemplateFile(index)" class="del-files el-icon-delete"></i>
            </li>
          </ul>
        </el-form-item>


      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button class="setBtn" @click="setAddMaterial">确 定</el-button>
        <el-button class="cananl" @click="cananlAddMaterial">取 消</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
  import Tinymce from '@/components/Tinymce'
  import {commonDict, commonDictOfRegion, commonDictGetNameByCaChe} from '@/api/utils'
  import {queryProjectNameOfProjectType} from '@/api/projectManage'
  import {
    queryProjectInfo,
    saveNoticeInfo,
    editNoticeInfo,
    getSystemVarList,
    getTableType,
    getTableNameInfoListByType,
    getTableColumnInfo
  } from '@/api/policy'
  import {addFileBatch, getFileListWithFileIds, ossFileSystemFileDownload} from '@/api/utils.js'
  import {uploadFilesOss} from '@/utils/uploadFilesOss'
  import {policyNoticeUpload, addMaterialUpload} from '@/utils/uploadPathMethods'
  import cloneDeep from "lodash/cloneDeep";

  export default {
    components: {Tinymce},
    props: ['noticeName', 'noticeType', 'noticeDetail'],
    data() {
      return {
        //地区--省
        province_list: [],
        //地区--市
        city_list: [],
        //地区--区
        district_list: [],
        //地区--镇街
        town_list: [],
        //level从3开始递减至0结束
        level: 3,
        parentId: 0,

        //基本信息--扶持方式
        supply_way_list: [
          {dictName: '事后补偿', dictId: '1'},
          {dictName: '无偿补偿', dictId: '2'},
          {dictName: '其他', dictId: '3'}
        ],

        // 基本信息--补助行业
        supply_industry_list: [],

        // 基本信息--难易程度
        difficulty_list: [
          {dictName: '普惠性', dictId: 1},
          {dictName: '竞争性', dictId: 2}
        ],

        //基本信息--有效期
        termDate: [],
        //基本信息--有效期-详情显示
        termDateName: "",

        //新增/编辑--提交的数据
        form: {},

        //补贴项目--弹窗
        project_table_visible: false,
        //补贴项目信息--数据
        project_list: [],
        //补贴项目信息--参数
        project_list_query: {
          page: 0,
          pageSize: 20
        },
        //补贴项目信息--总条数
        project_list_total: 0,
        //补贴项目信息--项目类型
        type_list: [],
        //补贴项目信息--项目名称
        projectDataList: [],
        //补贴项目信息--项目名称参数
        dataProjectForm: {
          page: 0,
          pageSize: 10
        },
        //补贴项目信息--已选项目
        select_projects: [],
        //补贴项目选中的值
        tempProjectsList: [],


        holdList: [],

        //补贴项目--申报条件--点击获取当前值
        clickTermDetail: {},


        //上传文件--数组
        fileList: [],


        //新增条件--弹窗
        addTermModal: false,

        //新增语句--弹窗
        addMentModal: false,

        //系统变量--弹窗
        systemModal: false,

        //判断是否新增
        isCreate: this.$route.query.is_create,

        //判断是否编辑
        isEdit: this.$route.query.is_edit,

        //判读是否详情页
        isCheck: this.$route.query.is_check,

        //判断是否显示申报条件
        isShowTerm: true,

        //新增条件--参数
        addTermParams: {
          autoMatch: 0,
        },

        //新增条件--监听输入框
        termParamsRules: {
          conditionDesc: [
            {required: true, message: '请输入条件描述', trigger: 'blur'}
          ],
          weightScore: [
            {required: true, message: '请输入权重分数', trigger: 'blur'}
          ],
          conditionExpression: [
            {required: true, message: '请输入子表达式', trigger: 'blur'}
          ],
        },

        //新增条件--判断状态新增/编辑；true:新增，false编辑
        isTermStyle: null,

        //新增语句--判断状态新增/编辑；true:新增，false编辑
        isMentStyle: null,

        //新增语句--参数
        addSentenceParams: {
          conditionDesc: "",
          tabelType: "",
          tableName: "",
          selectFieldName: "",
          whereFieldName: "",
          customSql: "",
        },

        //新增语句--表分类数据
        tableTypeData: [],

        //新增语句--form数据表数据
        tableNameData: [],

        //新增语句--select查询字段/where筛选字段数据
        fileldNameData: [],

        //点击新增语句，获取当前条件projectId
        termProjectId: "",

        //系统变量参数
        systemVarParam: {
          page: 0,
          pageSize: 20
        },

        //系统变量数据
        systemVarData: [],

        //系统变量数据总数
        systemVarDataTotal: "",

        //新增材料--弹窗
        materialModal: false,

        //新增材料--附件分类数据
        annexTypeData: [],

        //新增材料--上传附件列表
        templateFileList: [],

        //新增材料--提交参数
        materialParams: {
          templateFileIds: [],
        },

        //新增材料--监听输入不能为空
        materialRule: {
          materialType: [
            {required: true, message: '请选择附件分类', trigger: 'blur'}
          ],
          materialDesc: [
            {required: true, message: '请输入要求', trigger: 'blur'}
          ],
        },

        //新增材料--点击新增获取当前父级的值
        getMaterialParentIndex: '',
        keyWord: '',
      }
    },


    mounted() {
      // handleScroll为页面滚动的监听回调
      window.addEventListener('scroll', this.handleScroll);
      this.getRegionList();
    },
 
    methods: {
      //吸顶
        handleScroll(){
            let scrollTop=window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            let offsetTop=document.querySelector('.classBut').offsetTop;
            if(scrollTop>offsetTop){
                this.fixed=true;
            }else{
                this.fixed=false;
            }
        },
        //锚点
        jump(domId){
            // 当前窗口正中心位置到指定dom位置的距离 
            switch (domId)
            {
                case 'n-info':
                  // console.log("=========1==>");
                  this.classChoose = 5;
                  break;
                case 'n-subsidies':
                  // console.log("=========2==>");
                  this.classChoose = 6;
                  break;
                case 'n-interpretation':
                  this.classChoose = 7;
                  break;
                case 'n-original':
                  this.classChoose = 8;
                  break; 
            }



            //页面滚动了的距离
            let height = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop; 
            
            //指定dom到页面顶端的距离
            let dom = document.getElementById(domId);
            let domHeight = dom.offsetTop+70;
            
            //滚动距离计算
            var S = Number(height) - Number(domHeight);

            //判断上滚还是下滚
            if(S<0){
                //下滚
                S = Math.abs(S);
                window.scrollBy({ top: S, behavior: "smooth" });
            }else if(S==0){
                //不滚
                window.scrollBy({ top: 0, behavior: "smooth" });
            }else{
                //上滚
                S = -S
                window.scrollBy({ top: S, behavior: "smooth" });
            }
        },


      //获取地区信息
      getRegionList() {
        let params = {
          level: this.level,
          parentId: this.parentId
        };
        commonDictOfRegion(params).then(res => {
          if (res.code == 200) {
            if (this.level == 3) {
              this.province_list = res.data
            }
            if (this.level == 2) {
              this.city_list = res.data
            }
            if (this.level == 1) {
              this.district_list = res.data
            }
            if (this.level == 0) {
              this.town_list = res.data
            }
          } else {
            this.$message({type: 'error', message: res.message})
          }
        })
      },

      //获取地区信息
      getRegionListsss(level, parent) {
        let params = {
          level: level,
          parentId: parent
        };
        commonDictOfRegion(params).then(res => {
          if (res.code == 200) {

            if (level == 3) {
              this.province_list = res.data
            }
            if (level == 2) {
              this.city_list = res.data
            }
            if (level == 1) {
              this.district_list = res.data
            }
            if (level == 0) {
              this.town_list = res.data
            }
          } else {
            this.$message({type: 'error', message: res.message})
          }
        })
      },

      //获取字典数据
      getCommonData() {
        let params = {
          keyList: 'DICT:PROJECT_TYPE,DICT:SUBSIDYINDUSTRY_TYPE'
        };
        commonDict(params).then(res => {
          if (res.code == 200) {
            this.supply_industry_list = this.supply_industry_list.concat(res.data['DICT:SUBSIDYINDUSTRY_TYPE'])
            this.type_list = res.data['DICT:PROJECT_TYPE'];
          } else {
            this.$message({type: 'error', message: res.message})
          }
        })
      },

      //基本信息--选择省
      changeProvince(val) {
        this.form.cityId = "";
        this.form.districtId = "";
        this.$set(this.form, 'townId', "");
        this.parentId = val;
        this.level = 2;
        this.getRegionList()

      },
      //基本信息--选择市
      changeCity(val) {
        this.form.districtId = "";
        this.$set(this.form, 'townId', "");
        this.parentId = val;
        this.level = 1;
        this.getRegionList();
      },
      //基本信息--选择区
      changeDistrict(val) {
        this.parentId = val;
        this.level = 0;
        this.getRegionList();
        this.$set(this.form, 'townId', "");
      },

      //选择补贴项目--选择项目，按钮点击
      goSelectProject() {
        this.project_list_query.page = 0;
        this.project_list_query.pageSize = 10;
        this.holdList = JSON.parse(JSON.stringify(this.select_projects))
        let obj = {};
        this.holdList = this.holdList.reduce((cur, next) => {
          obj[next.projectId] ? '' : obj[next.projectId] = true && cur.push(next);
          return cur
        }, []);
        if (this.holdList.length !== 0) {
          this.tempProjectsList = JSON.parse(JSON.stringify(this.holdList));
        }
        this.getProjectList();
        this.project_table_visible = true;
      },

      //选择补贴项目--获取列表数据
      getProjectList() {
        queryProjectInfo(this.project_list_query).then(res => {
          this.project_list = res.data.data;
          this.project_list_total = res.data.total;
          if (this.tempProjectsList.length !== 0) {
            this.project_list.forEach(item => {
              this.tempProjectsList.forEach(data => {
                if (item.projectId == data.projectId) {
                  setTimeout(() => {
                    this.$refs.dataTable.toggleRowSelection(item);
                  }, 100)
                }
              })
            })
          }
        })
      },

      // 选择项目类型
      typeChange(e) {
        this.dataProjectForm.typeIdList = e
        this.getProjectListForType()
        this.getProjectList()
      },

      loadMore() {
        this.dataProjectForm.page = this.dataProjectForm.page + 10
        this.getProjectListForType()
      },

      //根据项目类型获取项目列表
      getProjectListForType(name) {
        let formData = new FormData
        formData.append('page', this.dataProjectForm.page)
        formData.append('pageSize', this.dataProjectForm.pageSize)
        formData.append('typeIdList', this.dataProjectForm.typeIdList)
        formData.append('projectName', this.dataProjectForm.projectName)
        queryProjectNameOfProjectType(formData).then(res => {
          if (res.code == 200) {
            if (this.dataProjectForm.page > 0) {
              this.projectDataList = this.projectDataList.concat(res.data.data)
            } else {
              if (res.data) {
                this.projectDataList = res.data.data
                this.proTotal = res.data.total
              } else {
                this.projectDataList = []
              }
            }
          }
        })
      },
      //选择补贴项目--选择项目名称
      companyRemoteMethod(query) {
        this.dataProjectForm.page = 0
        this.dataProjectForm.projectName = query
        if (this.dataProjectForm.typeIdList == undefined) {
          this.dataProjectForm.typeIdList = [];
        }
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.getProjectListForType()
        }, 200)

        this.project_list_query.projectName = query
        this.project_list_query.page = 0
        this.project_list_query.pageSize = 10
        this.getProjectList()
      },
      //选择补贴项目--关闭项目弹窗
      handleCloseProject() {
        this.project_table_visible = false
      },
      //选择补贴项目--项目选择
      projectChange(e) {
        if (e == '') {
          this.getProjectListForType();
        }
        this.project_list_query.page = 0;
        this.project_list_query.pageSize = 10;
        this.getProjectList()
      },
      //选择补贴项目--国家级别选择
      areaChange() {
        this.project_list_query.page = 0
        this.project_list_query.pageSize = 10
        this.getProjectList()
      },
      //选择补贴项目--选择省
      projectChangeProvince(val) {
        this.project_list_query.cityId = ''
        this.project_list_query.districtId = ''
        this.project_list_query.townId = ''
        this.parentId = val
        this.level = 2
        this.getRegionList()

        this.project_list_query.page = 0
        this.project_list_query.pageSize = 10
        this.getProjectList()
      },
      //选择补贴项目--选择市
      projectChangeCity(val) {
        this.project_list_query.districtId = ''
        this.project_list_query.townId = ''
        this.parentId = val
        this.level = 1
        this.getRegionList()

        this.project_list_query.page = 0
        this.project_list_query.pageSize = 10
        this.getProjectList()
      },
      //选择补贴项目--选择区
      projectChangeDistrict(val) {
        this.parentId = val
        this.level = 0
        this.getRegionList()

        this.project_list_query.page = 0
        this.project_list_query.pageSize = 10
        this.getProjectList()
