        this.addMentModal = true;
      },

      //选择补贴项目--新增语句--选中 表分类
      tableTypeHandle(val) {
        this.addSentenceParams.tableName = "";
        this.addSentenceParams.selectFieldName = "";
        this.addSentenceParams.whereFieldName = "";
        getTableNameInfoListByType({tableType: val}).then(res => {
          if (res.code == 200) {
            this.tableNameData = res.data;
          }
        })
      },

      //选择补贴项目--新增语句--选中 form数据表
      tableNameHandle(val) {
        this.addSentenceParams.selectFieldName = "";
        this.addSentenceParams.whereFieldName = "";
        getTableColumnInfo({tableName: val}).then(res => {
          if (res.code == 200) {
            this.fileldNameData = res.data;
            this.fileldNameData.push({columnName: "[count]", columnComment: "统计"})
          }
        })
      },

      //选择补贴项目--点击按钮--编辑语句
      editMentHandle(parentData, parentIndex, data, index) {
        this.isMentStyle = false;
        this.termProjectId = parentData.projectId;
        data.termIndex = parentIndex;
        data.mentIndex = index;


        //判断表分类是否有数据，有就调用方法
        if (data.tabelType && data.tabelType !== '') {
          this.tableTypeHandle(data.tabelType);
        }

        //判断form数据表是否有数据，有就调用方法
        if (data.tableName && data.tableName !== '') {
          this.tableNameHandle(data.tableName);
        }

        this.addSentenceParams = JSON.parse(JSON.stringify(data));
        this.addMentModal = true;
      },

      //选择补贴项目--确定新增语句
      setAddMent() {
        if (this.addSentenceParams.tabelType == "" && this.addSentenceParams.customSql == "") {
          this.$message.warning('请选择表分类或者输入自定义SQL');
          return
        }
        if (this.addSentenceParams.tabelType !== "" && this.addSentenceParams.tabelType !== null) {
          if (this.addSentenceParams.tableName == "") {
            this.$message.warning('请选择form数据表');
            return
          }
          if (this.addSentenceParams.selectFieldName == "") {
            this.$message.warning('请选择select查询字段');
            return
          }
          if (this.addSentenceParams.whereFieldName == "") {
            this.$message.warning('请选择where筛选字段');
            return
          }
          if (this.addSentenceParams.compareMark == "") {
            this.$message.warning('请输入筛选符号');
            return
          }
          if (this.addSentenceParams.compareValue == "") {
            this.$message.warning('请输入筛选值');
            return
          }
        }


        if (this.isMentStyle) {
          this.select_projects.forEach(item => {
            if (item.projectId == this.termProjectId) {
              item.policyApplyConditionVoList[this.addTermParams.termIndex].policyApplyConditionSqlVoList.push(this.addSentenceParams);
            }
          });
        } else {
          this.select_projects.forEach(item => {
            if (item.projectId == this.termProjectId) {
              item.policyApplyConditionVoList[this.addSentenceParams.termIndex].policyApplyConditionSqlVoList[this.addSentenceParams.mentIndex] = this.addSentenceParams;
            }
          });
        }
        //将ID译成中文
        this.select_projects.forEach(item => {
          item.policyApplyConditionVoList.forEach(data => {
            data.policyApplyConditionSqlVoList.forEach(child => {

              if (child.tableName && child.tableName !== "") {
                this.tableNameData.forEach(tableName => {
                  if (tableName.tableName == child.tableName) {
                    child.zhTableName = tableName.tableComment;
                  }
                })
              }

              if (child.selectFieldName && child.selectFieldName !== "") {
                this.fileldNameData.forEach(fileldName => {
                  if (child.selectFieldName == fileldName.columnName) {
                    child.zhSelectFieldName = fileldName.columnComment;
                  }
                })
              }

              if (child.whereFieldName && child.whereFieldName !== "") {
                this.fileldNameData.forEach(fileldName => {
                  if (child.whereFieldName == fileldName.columnName) {
                    child.zhWhereFieldName = fileldName.columnComment;
                  }
                })
              }


            })
          })
        });


        this.addSentenceParams = {
          conditionDesc: "",
          tabelType: "",
          tableName: "",
          selectFieldName: "",
          whereFieldName: "",
          customSql: "",
        }
        this.addMentModal = false;
      },

      //选择补贴项目--删除--语句
      delMentHandle(parentData, parentIndex, data, index) {
        this.select_projects.forEach(item => {
          if (parentData.projectId === item.projectId) {
            item.policyApplyConditionVoList[parentIndex].policyApplyConditionSqlVoList.splice(index, 1);
          }
        })
      },

      //选择补贴项目--取消--编辑/新增语句
      callMentHandle() {
        this.addSentenceParams = {
          conditionDesc: "",
          tabelType: "",
          tableName: "",
          selectFieldName: "",
          whereFieldName: "",
          customSql: "",
        }
        this.addMentModal = false;
      },

      //点击系统变量
      systemHandle() {
        this.systemVarParam.keyWord = this.keyWord
        getSystemVarList(this.systemVarParam).then(res => {
          if (res.code == 200) {
            if (this.systemVarParam.page > 0) {
              this.systemVarData = this.systemVarData.concat(res.data.data);
            } else {
              this.systemVarData = res.data.data;
            }

            this.systemVarDataTotal = res.data.total;
          }
        });
        this.systemModal = true
      },

      //获取更多系统变量
      getMoreSysData() {
        this.systemVarParam.page += 20;
        this.systemHandle();
