      //限制输入数字
      inputXz(e, num) {
        if (Number(e.target.value) > 100) {
          e.target.value = 100;
        }
        if (num == 0) {
          this.addTermParams.weightScore = e.target.value
        }
        // e.target.value = (e.target.value.match(/^\d*(\.?\d{0,2})/g)[0]) || null;
      },

      //新增补助
      addSubsidyHandle(data, index) {
        let obj = {
          projectId: data.projectId,
          projectLevel: "",
          subsidyPriceMin: "",
          subsidyPriceMax: "",
          subsidyDesc: "",
        };


        this.select_projects = this.select_projects.map(item => {
          if (item.projectId === data.projectId) {
            item.isDel = true;
            item.policyNoticeProjectRelList.push(obj);
          }
          return item;
        })
      },

      //删除补助
      delProjectHandle(data, index) {
        this.select_projects = this.select_projects.map(item => {
          if (item.projectId === data.projectId) {
            item.policyNoticeProjectRelList.splice(index, 1);
            if (item.policyNoticeProjectRelList.length == 1) {
              item.isDel = false;
            } else {
              item.isDel = true;
            }
          }
          return item;
        })
      },

      //选择补贴项目--移除项目子项
      removeProjectItem(item, index) {
        this.select_projects.splice(index, 1);
        this.tempProjectsList = JSON.parse(JSON.stringify(this.select_projects));
      },

      // 返回需要上传的地址
      UploadUrl() {
        return '/policy/api/policy/policyNoticeInfo/uploadFile' // 返回需要上传的地址
      },

      //文件删除
      handleRemove(file, fileList) {
        this.$message.success('删除成功')
        this.fileList = fileList
        this.fileNameList.forEach(item => {
          if (file.uid == item.uid) {
            this.fileIdsList.forEach((list, index) => {
              if (item.filesId == list) {
                this.fileIdsList.splice(index, 1)
              }
            })
          }
        })
      },

      // 上传文件
      handlePoliceUpload(option) {
        if (option.file.name.length >= 150) {
          this.$message.error('文件名称超长，请检查');
          this.fileList = JSON.parse(JSON.stringify(this.fileList))
          return;
        }
        // 调用 ali-oss 中的方法
        let fileName = option.file.name.split('.');
        let filePath = policyNoticeUpload('政策', fileName[0]) + '.' + fileName[fileName.length-1]
        uploadFilesOss(filePath, option.file, option).then(res => {
          if (res) {
            let FileList = [];
            let addFile = {
              fileName: option.file.name,
              filePath: filePath,
              fileSize: option.file.size,
              fileType: fileName[fileName.length-1],
              parentType: 4
            }
            FileList.push(addFile)
            let params = {
              companyId: this.$store.getters.companyId,
              companyName: this.$store.getters.companyName,
              addFileDtos: FileList,
              haveView: true
            }

            addFileBatch(params).then(response => {
              if (response.code == 200) {
                this.$message.success('上传成功');
                this.fileList.push(response.data[0]);
              }
            })
          }
        })
      },

      // 删除文件
      delFileHandle(index) {
        this.fileList.splice(index, 1);
      },

      // 确认提交
      noticeHandSubmit(formName) {
        this.$refs[formName].validate((valid, error) => {
          if (valid) {
            //监听名称不能为空
            if (this.noticeName == undefined || this.noticeName == '') {
              this.$message.warning('请输入名称');
              return
            }

            //监听省不能为空
            if (this.form.provinceId == undefined || this.form.provinceId == '' || this.form.provinceId == null) {
              this.$message.warning('请选择地区');
              return
            }

            //监听发布单位不能为空
            if (this.form.publishOrgan == undefined || this.form.publishOrgan == '' || this.form.publishOrgan == null) {
              this.$message.warning('请输入发布单位');
              return
            }
            //监听发布日期不能为空
            if (this.form.publishDate == undefined || this.form.publishDate == '' || this.form.publishDate == null) {
              this.$message.warning('请选择发布日期');
              return
            }

            //监听难易程度不能为空
            if (this.form.applyDifficulty == undefined || this.form.applyDifficulty == '' || this.form.applyDifficulty == null) {
              this.$message.warning('请选择难易程度');
              return
            }

            //监听有效期不能为空
            if (this.termDate == null || this.termDate.length == 0) {
              this.$message.warning('请选择有效期');
              return
            }

            //监听补贴项目信息本能为空
            if (this.select_projects.length == 0) {
              this.$message.warning('请选择补贴项目信息');
              return
            }

            //监听--项目级别是否有重复级别
            if (this.select_projects.length !== 0) {
              for (let i = 0; i <= this.select_projects.length; i++) {
                if (this.select_projects[i] !== undefined && this.select_projects[i].policyNoticeProjectRelList.length !== 0 && this.select_projects[i].policyNoticeProjectRelList.length > 1) {
                  for (let j = 0; j <= this.select_projects[i].policyNoticeProjectRelList.length; j++) {
                    //项目级别是否有重复级别
                    if (this.select_projects[i].policyNoticeProjectRelList[j + 1] !== undefined) {
                      if (this.select_projects[i].policyNoticeProjectRelList[j].projectLevel === this.select_projects[i].policyNoticeProjectRelList[j + 1].projectLevel) {
                        this.$message.warning(`${this.select_projects[i].projectName} 中存在相同的项目级别，请重新选择`);
                        return
                      }
                    }
                  }
                }
              }
            }

            //监听--材料清单--模板文件不能为空
            // if (this.select_projects.length !== 0) {
            //   this.select_projects.forEach(item=>{
            //     if(item.policyApplyMaterialLists.length !== 0){
            //       item.policyApplyMaterialLists.forEach(data=>{
            //         if(data.templateFileIds.length == 0){
            //           this.$message.warning(`${item.projectName} 中的材料清单，请选择模板文件`);
            //           throw new Error()
            //         }
            //       })
            //     }
            //
            //   })
            // }

            //有效期数据重组
            this.form.validDateBegin = this.termDate[0];
            this.form.validDateEnd = this.termDate[1];

            //上传文件数据重组
            if (this.fileList.length !== 0) {
              let file = this.fileList.map(item => {
                return item.fileId
              });

              this.form.fileIds = file.join(',');
            }


            //监听--项目级别--数据重组
            let selectArr = [];
            let mentArr = [];
            let material = [];
            this.select_projects.forEach(item => {
              item.policyNoticeProjectRelList.forEach(data => {
                if (data.projectLevel == '' || data.projectLevel == null || data.projectLevel == undefined) {
                  this.$message.warning('请选择项目级别');
                  throw new Error()
                }


                if (data.subsidyPriceMin == '' || data.subsidyPriceMin == null || data.subsidyPriceMin == undefined) {
                  data.subsidyPriceMin = 0
                }


                if (data.subsidyPriceMax == '' || data.subsidyPriceMax == null || data.subsidyPriceMax == undefined) {
                  data.subsidyPriceMax = 0
                }

                if (Number(data.subsidyPriceMin) > Number(data.subsidyPriceMax)) {
                  this.$message.warning(`${item.projectName} 中最低补助不能大于最高补助，请重新输入`);
                  throw new Error()
                }

                // data.subsidyPriceMin = data.subsidyPriceMin * 10000
                // data.subsidyPriceMax = data.subsidyPriceMax * 10000

                let obj = {
                  projectId: data.projectId,
                  projectLevel: data.projectLevel,
                  subsidyDesc: data.subsidyDesc,
                  subsidyPriceMin: data.subsidyPriceMin * 10000,
                  subsidyPriceMax: data.subsidyPriceMax * 10000,
                  subsidyId: data.subsidyId ? data.subsidyId : "",
                };
                selectArr.push(obj)
              })


              //申报条件--数据重组
              if (item.policyApplyConditionVoList && item.policyApplyConditionVoList.length !== 0) {
                item.policyApplyConditionVoList.forEach(ment => {
                  ment.policyApplyConditionSqlDtoList = []
                  ment.policyApplyConditionSqlVoList.forEach(child => {
                    ment.policyApplyConditionSqlDtoList.push(child)
                  });
                  mentArr.push(ment)
                })
              }

              //材料清单--数据重组
              if (item.policyApplyMaterialLists && item.policyApplyMaterialLists.length !== 0) {
                let materialLists = cloneDeep(item.policyApplyMaterialLists)

                materialLists.forEach(item => {
                  if (item.templateFileIds.length !== 0) {
                    let fileListArr = item.templateFileIds.map(data => {
                      return data.fileId;
                    })
                    item.templateFileIds = fileListArr.join(',');
                    material.push(item);
                  }
                })
              }
            });

            this.form.policyNoticeProjectRelList = selectArr;
            this.form.policyApplyConditionDtoList = mentArr;
            this.form.policyApplyMaterialLists = material;

            //类型赋值
            this.form.noticeType = this.noticeType;

            //名称赋值
            this.form.noticeName = this.noticeName;


            //保存接口--调用
            if (this.$route.query.is_edit) {
              this.form.noticeId = this.$route.query.noticeId;

              editNoticeInfo(this.form).then(res => {
                if (res.code == 200) {
                  this.$message.success('编辑成功!')
                  this.form = {
                    noticeType: 1,
                    noticeContent: '',
                    noticeSummary: ''
                  };
                  this.project_list_query = {
                    page: 0,
                    pageSize: 10
                  }
                  this.fileList = [];
                  this.select_projects = [];
                  this.tempProjectsList = [];
                  this.termDate = [];
                  this.$refs.editor.setContent("");
                  this.$refs.editor2.setContent("");
                  // this.$store.commit('SET_POLICY_SCROLL', 0)
                  this.closeCurrent(this.$store.state.tagsView.visitedViews, this.$route, '/policy')
                }
              })
            } else {
              saveNoticeInfo(this.form).then(res => {
                if (res.code == 200) {
                  this.$message.success('新建成功!')
                  this.form = {
                    noticeType: 1,
                    noticeContent: '',
                    noticeSummary: ''
                  }
                  this.project_list_query = {
                    page: 0,
                    pageSize: 10
                  }
                  this.fileList = []
                  this.select_projects = []
                  this.tempProjectsList = [];
                  this.termDate = [];
                  this.$refs.editor.setContent("");
                  this.$refs.editor2.setContent("");
                  this.$emit('naticeByValue', this.form);
                  this.$store.commit('SET_POLICY_SCROLL', 0)
                  this.closeCurrent(this.$store.state.tagsView.visitedViews, this.$route, '/policy')
                }
              })
            }
          } else {
            return false
          }
        })
      },

      //详情--地区一次获取
      getAddressDetail() {
        this.getRegionListsss(3, 0);
        if (this.form.provinceId) {
          this.getRegionListsss(2, this.form.provinceId)
        }
        if (this.form.cityId) {
          this.getRegionListsss(1, this.form.cityId)
        }
        if (this.form.districtId) {
          this.getRegionListsss(0, this.form.districtId)
        }
      },

      //详情-获取文件
      getFileDetail(fileIds) {
        let fileArr = fileIds.split(',');
        getFileListWithFileIds({fileIds: fileArr, haveView: false}).then(res => {
          if (res.code == 200) {
            this.fileList = res.data
          } else {
            this.$message.warning(res.message)
          }
        })
      },

      //取消--点击
      handlerCancel() {
        this.form = {
          noticeType: 1,
          noticeContent: '',
          noticeSummary: ''
        }
        this.project_list_query = {
          page: 0,
          pageSize: 10
        }
        this.fileList = []
        this.select_projects = [];
        this.tempProjectsList = [];
        this.termDate = [];
        if(this.is_edit){
          this.$refs.editor.setContent("");
          this.$refs.editor2.setContent("");
        }

        this.$emit('naticeByValue', this.form);
        this.closeCurrent(this.$store.state.tagsView.visitedViews, this.$route, '/policy')
      },

      //下载文件
      downFileHandle(data) {
        if (data.filePath) {
          ossFileSystemFileDownload(data).then(res => {
            if (res.code == 200) {
              window.open(res.data);
              this.$message.success('下载成功')
            }
          })
        }
      },

      //点击--新增材料
      addMaterialBtnHandle(index) {
        this.materialModal = true;
        this.getMaterialParentIndex = index;
        this.getAnnexTypeData();
      },

      //新增材料--获取附件分类数据
      getAnnexTypeData() {
        let params = {
          keyList: 'DICT:MATERIALTYPE',
          companyId: this.$store.getters.companyId
        };
        commonDict(params).then(res => {
          if (res.code == 200) {
            this.annexTypeData = res.data['DICT:MATERIALTYPE'];
          } else {
            this.$message({type: 'error', message: res.message})
          }
        })
      },

      //新增材料--上传模板
      handleTemplateUpload(option) {
        if (option.file.name.length >= 150) {
          this.$message.error('文件名称超长，请检查');
          this.fileList = JSON.parse(JSON.stringify(this.fileList))
          return;
        }
        // 调用 ali-oss 中的方法
        let fileName = option.file.name.split('.');
        let filePath = addMaterialUpload('政策通知', fileName[0]) + '.' + fileName[fileName.length-1]
        uploadFilesOss(filePath, option.file, option).then(res => {
          if (res) {
            let FileList = [];
            let addFile = {
              fileName: option.file.name,
              filePath: filePath,
              fileSize: option.file.size,
              fileType: fileName[fileName.length-1],
              parentType: 4
            }
            FileList.push(addFile)
            let params = {
              companyId: this.$store.getters.companyId,
              companyName: this.$store.getters.companyName,
              addFileDtos: FileList,
              haveView: true
            }

            addFileBatch(params).then(response => {
              if (response.code == 200) {
                this.$message.success('上传成功');
                this.templateFileList.push(response.data[0]);
              }
            })
          }
        })
      },

      //新增材料--删除模板
      delTemplateFile(index) {
        this.templateFileList.splice(index, 1);
      },

      //新增材料--确定新增材料
      setAddMaterial() {
        this.$refs.materialForm.validate((valid, error) => {
          if (valid) {
            // if (this.templateFileList.length == 0) {
            //   this.$message.warning('请选择模板');
            //   return
            // }
            this.materialParams.templateFileIds = this.templateFileList;

            this.annexTypeData.forEach(item => {
              if (item.dictId == this.materialParams.materialType) {
                this.materialParams.materialTypeName = item.dictName
              }
            });


            this.materialParams.projectId = this.select_projects[this.getMaterialParentIndex].projectId;

            this.select_projects[this.getMaterialParentIndex].policyApplyMaterialLists.push(this.materialParams);

            this.materialParams = {
              templateFileIds: [],
            };
            this.templateFileList = [];
            this.materialModal = false;
          }
        })
      },

      //新增材料--取消新增材料
      cananlAddMaterial() {
        this.materialParams = {
          templateFileIds: [],
        };
        this.templateFileList = [];
        this.materialModal = false;
      },


      //材料清单--删除模板文件
      deleteTemplateFile(index, nums, childNum) {
        this.select_projects[index].policyApplyMaterialLists[nums].templateFileIds.splice(childNum, 1);
      },

      //材料清单--移除清单
      deleteDetailed(index, nums) {
        this.select_projects[index].policyApplyMaterialLists.splice(nums, 1);
        this.select_projects = cloneDeep(this.select_projects);
      },
    },
    directives: {
      drag: {
        // 系统变量弹窗滑动--方法
        bind: function (el) {
          let ohead = el.querySelector('.modal-heards');   //获取当前元素
          ohead.onmousedown = (e) => {
            //算出鼠标相对元素的位置
            let disX = e.clientX - el.offsetLeft;
            let disY = e.clientY - el.offsetTop;
            let left = '';
            let top = '';
            document.onmousemove = (e) => {
              //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
              left = e.clientX - disX;
              top = e.clientY - disY;

              //绑定元素位置到positionX和positionY上面
              //移动当前元素
              el.style.left = left + 'px';
              el.style.top = top + 'px';
            };
            document.onmouseup = (e) => {
              document.onmousemove = null;
              document.onmouseup = null;
            };
          };
        }
      },
      loadMore: {
        bind(el, binding) {
          // 获取element-ui定义好的scroll盒子
          const SELECTWRAP_DOM = el.querySelector('.el-select-dropdown .el-select-dropdown__wrap')
          SELECTWRAP_DOM.addEventListener('scroll', function () {

            const CONDITION = this.scrollHeight - this.scrollTop <= this.clientHeight
            if (CONDITION) {
              binding.value()
            }
          })
        }
      }
    },

    watch: {
      //获取详情
      noticeDetail(newVal, oldVal) {
        console.log(newVal, oldVal);
        if (this.$route.query.is_edit || this.$route.query.is_check) {
          if (JSON.stringify(newVal) !== '{}') {
            if (this.noticeType == 1) {
              this.getCommonData();
              this.form = newVal;

              this.getAddressDetail();

              //申报条件--数据重组
              this.form.policyNoticeProjectSubsidyRelVos.forEach(item => {
                // 项目级别数据重组

                if (item.policyNoticeProjectSubsidyVos.length !== 0) {
                  item.policyNoticeProjectSubsidyVos.forEach(data => {
                    data.projectId = item.projectId;
                    if (data.subsidyPriceMin && data.subsidyPriceMin !== '' && data.subsidyPriceMin > 1000) {
                      // data.subsidyPriceMin = data.subsidyPriceMin.toString();
                      // data.subsidyPriceMin = data.subsidyPriceMin / 10000
                      this.$set(data,'subsidyPriceMin',data.subsidyPriceMin / 10000)
                    }
                    if (data.subsidyPriceMax && data.subsidyPriceMax !== '' && data.subsidyPriceMax > 1000) {
                      // data.subsidyPriceMax = data.subsidyPriceMax.toString();
                      // data.subsidyPriceMax = data.subsidyPriceMax / 10000
                      this.$set(data,'subsidyPriceMax',data.subsidyPriceMax / 10000)
                    }
                  })
                  if (item.policyNoticeProjectSubsidyVos.length > 1) {
                    item.isDel = true;
                  }
                  this.$set(item,'policyNoticeProjectRelList',item.policyNoticeProjectSubsidyVos)
                  // item.policyNoticeProjectRelList = item.policyNoticeProjectSubsidyVos;
                } else {
                  this.$set(item, 'policyNoticeProjectSubsidyVos', [])
                }

                if (item.policyApplyMaterialListVos) {
                  if (item.policyApplyMaterialListVos.length !== 0) {
                    item.policyApplyMaterialLists = item.policyApplyMaterialListVos.map(item => {
                      if (item.templateFileIds !== "") {
                        item.templateFileIds = item.templateFileIds.split(',');
                        getFileListWithFileIds({fileIds: item.templateFileIds, haveView: false}).then(res => {
                          if (res.code == 200) {
                            item.templateFileIds = res.data;
                          } else {
                            this.$message.warning(res.message)
                          }
                        })
                      }
                      return item;
                    });
                  } else {
                    this.$set(item, 'policyApplyMaterialLists', [])
                  }
                }

              });

              this.select_projects = this.form.policyNoticeProjectSubsidyRelVos;

              //判断有效期是否存在
              if (this.form.validDateBegin && this.form.validDateEnd) {
                this.termDate = [this.form.validDateBegin, this.form.validDateEnd];
              } else {
                this.termDate = [];
              }

              //文件转列表
              if (this.form.fileIds && this.form.fileIds !== "") {
                this.getFileDetail(this.form.fileIds)
              }

              //当页面是详情页的时候，接口传id,获取中文名
              if (this.$route.query.is_check) {
                //有效期转换
                this.termDateName = `${this.form.validDateBegin} 至 ${this.form.validDateEnd}`
              }

            }
          }
        }
      }
    }
  }
</script>

