    /deep/ .el-form-item__label {
      font-weight: normal;
      white-space: nowrap;
      color: rgba(68, 68, 68, 0.75);
    }

    /deep/ .el-form-item__content {
      width: 83%;
      display: flex;
    }

    .user-input {
      /deep/ .el-input__inner {
        border: 0;
        border-radius: 0;
        border-bottom: 1px solid rgba(68, 68, 68, 0.3);
      }

      &.input {
        width: 100%;
        border-radius: 0;
        border: 0;
        border-bottom: 1px solid rgba(68, 68, 68, 0.3);

        &:focus {
          border: 0;
          border-bottom: 1px solid rgba(68, 68, 68, 0.3);
        }
      }
    }

    .user-select {
      width: 100%;
    }

    .switch-btn {
      font-size: 14px;
      color: #fff;
      background: rgba(5, 170, 255, 1);
      margin-left: 10px;

      &:active {
        opacity: .5;
      }
    }

    .modal-radio {
      display: flex;
      align-items: center;

      /deep/ .el-form-item__content {
        margin-left: 0 !important;
      }
    }

    .dialog-footer {
      .setBtn {
        color: #fff;
        background: rgba(5, 170, 255, 1);
      }

      .cananl {
        background: #fff;
        color: rgba(68, 68, 68, 1);
      }
    }

    .system-btn {
      text-align: right;
      padding-right: 30px;

      .btn {
        width: 80px;
        height: 30px;
        background: #00A5FF;
        color: #fff;
        outline: none;
        cursor: pointer;
        border-radius: 4px;
        border: 0;
        padding: 0;

        &:active {
          opacity: 0.5;
        }
      }
    }
  }

  .ment-modal {
    /deep/ .el-form-item__label {
      line-height: 20px;
    }

    /deep/ .el-form-item__label {
      white-space: inherit;
    }

    .ment-text {
      display: flex;
      align-items: center;

      /deep/ .el-form-item__content {
        margin-left: 0 !important;
      }
    }

    .set-file-btn {
      border: 0;
      outline: none;
      padding: 6px;
      border-radius: 4px;
      color: #fff;
      cursor: pointer;
      background: #05aaff;

      &:active {
        opacity: 0.5;
      }
    }

    .del-files {
      color: red;
      margin-left: 10px;
      cursor: pointer;
      font-size: 14px;
    }
  }

  .system-model {
    width: 400px;
    height: 358px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    background: #fff;
    z-index: 3000;
    box-shadow: 0 1px 3px rgba(0, 0, 0, .3);
    border-radius: 2px;

    .modal-heards {
      cursor: move;
      padding: 20px 20px 16px;
      font-size: 18px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .icon-close {
        cursor: pointer;
        color: #909399;
        font-size: 15px;
      }
    }

    .modal-content {
      .sys-var-more {
        background: #fff;
        padding-top: 10px;

        div {
          font-size: 14px;
          text-align: center;
        }

        .get-sys-more {
          cursor: pointer;
        }
      }

      .sum-data {
        font-size: 14px;
        padding-left: 20px;
        margin-bottom: 5px;

        .num {
          color: red;
        }
      }
    }

  }

  .address-bar {
    display: flex;
    align-items: center;
    margin-bottom: 22px;
    padding-left: 28px;

    .address-title {
      margin-right: 14px;
      white-space: nowrap;
      font-size: 14px;
      color: #606266;
    }
  }

  .file-list {
    display: flex;
    align-items: center;

    .icon-del-file {
      cursor: pointer;
      margin-left: 10px;
      margin-top: 3px;
    }

    .down-files {
      cursor: pointer;
      margin-top: 2px;
      margin-left: 10px;
    }
  }

  .editorHtml {
    .wscnph {
      width: auto;
      max-width: 100%;
    }
  }

</style>



